<template>
  <div class="col-md-4 mt-4 cursor" @click="$emit('openModal', 원룸.id)">
    <div class="card">
      <div class="card-head">
        <img :src="원룸.image" class="room-img">
      </div>
      <div class="card-body">
        <h4>{{ 원룸.title }}</h4>
        <p class="price">{{ 원룸.price }} 원</p>
      </div>
    </div>
  </div>
  <div class="black-bg" v-if="openModal == true">
    <div class="white-bg">
      <img :src="원룸들[누른거].image" class="room-img">
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <p>
        <input v-model.number="month">
        <!-- <input @input="month = $event.target.value"> -->
      </p>      
      <p> {{ month }}개월 선택함 : {{ 원룸들[누른거].price * month}} 원</p>
      <button @click="openModal == false">닫기버튼</button>
      <!-- <button @click="$emit('closeModal')">닫기버튼</button> -->
    </div>
  </div>   
</template>

<script>
export default {
  name: 'CardView',
  props : {
    원룸 : Object,
  },
 
}
</script>

<style scoped>
.card-head {background: #efefef;}
.room-img {width:100%; height:200px;}
.price {text-align: right;}
</style>