<template>
  <div class="modal-bg" v-if="openModal == true" @click="$emit('closeModal')">
    <div class="modal-wrap">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel"><h4>{{ 원룸들[누른거].title }}</h4></h1>
          <button type="button" class="btn-close" @click="$emit('closeModal')"></button>
        </div>
        <div class="modal-body">
          <img :src="원룸들[누른거].image" class="room-img">
          <p class="mt-2">{{ 원룸들[누른거].content }}</p>
        </div>
        <div class="modal-footer justify-content-center">
          <button type="button" class="btn btn-secondary" @click="$emit('closeModal')">Close</button>
        </div>
      </div>
    </div>    
    <!-- <div class="white-bg">
      <img :src="원룸들[누른거].image" class="room-img">
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <button @click="openModal == false">닫기버튼</button>
    </div> -->
  </div>
</template>

<script>
export default {
  name: 'ModalView',
  data(){
    return {
      month : 1,
    }
  },
  watch : {
    month(a){
      // if (a>=13){
      //   alert('13이사 입력하지 마셈')
      // }
    if (isNaN(a) == true){
        alert('문자입력하지마라');
        this.month = 1;
      }
    },
  },
  props : {
    원룸들 : Array,
    누른거 : Number,
    openModal : Boolean,
  },
  beforeUpdate(){
      if (this.month == 2){
        alert('2개월은 너무 적음.. 안팝니다')
      }
  }  
}
</script>

