<template>
  <div class="container">
    <Transition name="fade">
      <ModalView @closeModal="openModal = false;" :원룸들="원룸들" :누른거="누른거" :openModal="openModal" />
    </Transition>
    <!-- <div class="menu01">
      <a v-for="(a, i) in 메뉴들" :key="i">{{ a }} /</a>
    </div> -->

    <!-- <DiscountBanner v-if="showDiscount == true" />
    <p>지금 결제하면 {{amount}}% 할인</p> -->
    <div style="display:flex; justify-content: end; "> 
      <button @click="priceSort"  type="button" class="btn btn-outline-primary" style="margin-right:10px;">가격순정렬</button>
      <button @click="sortBack"   type="button" class="btn btn-outline-primary">되돌리기</button>
    </div>     
    <div class="row">
      <CardView @openModal="openModal = true; 누른거 = $event" :원룸="원룸들[i]" v-for="(작명,i) in 원룸들" :key="작명" />
    </div>      
  </div>
</template>

<script>
//import DiscountBanner from '@/components/DiscountBanner.vue';
import ModalView from '@/components/ModalView.vue';
import CardView from '@/components/CardView.vue';
import projectviewData from '@/data/projectviewData';
export default {
  name: 'ProjectView',
  data(){
    return{
    projectviewData,
    showDiscount : true,
    원룸들오리지널 : [...projectviewData],
    amount : 30,
    누른거 : 0,
    원룸들 : projectviewData,
    openModal : false,
    신고수 : [0,0,0],
    메뉴들 : ['home', 'shop', 'About'],
    price : [50, 80, 90],
    products : [ '역삼동원룸', '천호동원룸', '마포구원룸' ]      
    }
  },
  methods : {
    increase(){
      this.신고수 += 1;
    },
    sortBack(){
      this.원룸들 = [...this.원룸들오리지널];
    },
    priceSort(){
      this.원룸들.sort(function(a,b){
        return a.price - b.price
      })
    },
  },
  mounted(){
    setTimeout(() => {
      this.showDiscount = false;
    }, 2000);
    setInterval(()=>{
      // this.amount--;
  }, 1000);
  },      
  components: {
    //DiscountBanner,
    ModalView,
    CardView,    
  },
}
</script>

